.App {
  text-align: center;
  scroll-behavior: smooth;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.slide-container {
  position: "relative";
  display: "inline";
  width: 100%;
  height: 100vh;
  margin: auto; 
  z-index: -1;
  background-size: cover;
  object-fit: cover;
}

h3 {
  text-align: center; }

.each-slide > div {
  position: "absolute";
  display: relative;
  align-items: center;
  justify-content: center;
  height: 100vh;
  z-index: -1;
}

.each-slide span {
  position: "absolute";
  padding: 20px;
  font-size: 20px;
  background: #efefef;
  text-align: center;
  z-index: -1;
}

.each-fade {
  position: "absolute";
  display: relative;
  z-index: -1;
}

.each-fade .image-container {
  position: "absolute";
  width: 75%;
  overflow: hidden;
  z-index: -1;
}

.each-fade .image-container img {
  position: "absolute";
  width: 100%;
  z-index: -1;
}

.each-fade h2 {
  width: 25%;
  display: relative;
  justify-content: center;
  align-items: center;
  margin: 0;
  background: #adceed;
  z-index: -1;
}

.textOver {
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}

.textOver2 {
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}
